body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {margin: 0; font-family: sans-serif; width: 100vw; height: 100vh; overflow: hidden;}
.tooltip {pointer-events: none; position: absolute; z-index: 9; font-size: 12px; padding: 8px; background: #000; color: #fff; min-width: 160px; max-height: 240px; overflow-y: hidden;}
.tooltip.interactive {pointer-events: all; background: #fcfcfc; color: #444; overflow-y: auto;}


#control-panel  {
height: 150px;
background-color: #fff;
width: 200px;
position: fixed;
top: 20px;
left: 20px;
font-size: 14px;
padding: 20px;
font-family: Arial, Helvetica, sans-serif;
z-index: 1;
}

#info {
left: initial;
right: 20px;
width: 300px;

}

#info * {
font-size: 12px !important;
}

table td {
border:1px solid #ccc;
}

select {
display: block;
margin-bottom: 20px;
width: 80%;
}

#deckgl-wrapper {
    visibility: hidden;
}